import React from "react";
import "./CurrencyIcon.css";

const CurrencyIcon = ({ currency, size = 15 }: any) => {
  const formattedCurrency =
    currency.length > 5
      ? currency
          .replace("USDT", "")
          .replace("BUSD", "")
          .replace("-PERP", "")
          .replace("USD", "")
      : currency;

  return (
    <div
      className="CurrencyIcon"
      style={{
        width: size,
        height: size,
        borderRadius: size,
        backgroundSize: size,
        backgroundImage: `url(https://3commas.io/currency/icon/${formattedCurrency.toLowerCase()}.png)`,
      }}
    ></div>
  );
};

export default CurrencyIcon;
